
.albumArt {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 100px 0 20px 0;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 80%;
    margin: 0 auto;
}

.albumArt__link {
    position: absolute;
    background-color: transparent;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.albumArt__wrapper {
    -webkit-transform: rotateZ(5deg);
    -webkit-backface-visibility: hidden;
    padding: 2px;
}

.albumArt__wrapper:hover {
    -webkit-transform: translateY(-2%);
        -ms-transform: translateY(-2%);
            transform: translateY(-2%);
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
}

.albumArt__lucidDreamer {
    background: url('https://f4.bcbits.com/img/a1537340904_10.jpg') no-repeat 50% 50%;
    height: 300px;
    width: 300px;
    background-size: cover;
    -webkit-transform: rotate(-15deg);
        -ms-transform: rotate(-15deg);
            transform: rotate(-15deg);
    border: 2px solid black;
    outline: 4px solid white;
}

.albumArt__delusions {
    background: url('https://f4.bcbits.com/img/a3698577754_10.jpg') no-repeat 50% 50%;
    height: 300px;
    width: 300px;
    background-size: cover;
    -webkit-transform: rotate(5deg);
        -ms-transform: rotate(5deg);
            transform: rotate(5deg);
    border: 2px solid black;
    outline: 4px solid white;
}

.albumArt__parade {
    background: url('https://f4.bcbits.com/img/a1180755077_10.jpg') no-repeat 50% 50%;
    height: 300px;
    width: 300px;
    background-size: cover;
    -webkit-transform: rotate(-10deg);
        -ms-transform: rotate(-10deg);
            transform: rotate(-10deg);
    border: 2px solid black;
    outline: 4px solid white;
}

.albumArt__journey {
    background: url('https://f4.bcbits.com/img/a1024485109_10.jpg') no-repeat 50% 50%;
    height: 300px;
    width: 300px;
    background-size: cover;
    -webkit-transform: rotate(5deg);
        -ms-transform: rotate(5deg);
            transform: rotate(5deg);
    border: 2px solid black;
    outline: 4px solid white;
}



